import { Component } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HfButtonComponent, HfInputComponent, HfToastService, inputMsgType } from '@monorepo/ui';
import { FormControl, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { Subscription } from 'rxjs';
import { CapitolcityoilFooterComponent } from './capitolcityoil-footer/capitolcityoil-footer.component';

@Component({
  selector: 'lib-capitolcityoil-howitworks',
  standalone: true,
  imports: [
    CommonModule,
    HfInputComponent,
    HfButtonComponent,
    CapitolcityoilFooterComponent
  ],
  templateUrl: './capitolcityoil-howitworks.component.html',
  styleUrl: './capitolcityoil-howitworks.component.scss',
})
export class CapitolcityoilHowitworksComponent {

  constructor(
    private http: HttpClient,
    private toastService: HfToastService,
  ) { }

  emailKey = /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
  emailApiUrl = `https://demo-api.heatfleet.com/api/customer/offer/savecontactdata`;

  emailAddress: FormControl = new FormControl('', [
    Validators.pattern(this.emailKey), Validators.required
  ]);

  emailAddressIM = '';
  emailAddressMT: inputMsgType = '';
  isValidEmailSubscription: Subscription;

  getEmailSubscribe() {
    if (this.emailAddress.valid) {
      this.http.post(this.emailApiUrl, { 'email': this.emailAddress.value }).subscribe((data) => {
        if (data) {
          console.log(data)
          this.toastService.toastMessage.next({ title: 'Subscription done', msg: 'Thank You for subscribing.', type: 'success' });
          this.emailAddress.setValue(null);
        }
      });
    } else {
      if (this.emailAddress.invalid && this.emailAddress.errors) {
        this.emailAddressMT = 'error'
        if (this.emailAddress.errors['required']) {
          this.emailAddressIM = 'Email is required';
        }
        if (this.emailAddress.errors['pattern']) {
          this.emailAddressIM = 'Enter valid email';
        }
        this.isValidEmailSubscription = this.emailAddress.statusChanges.subscribe((status: any) => {
          if (status === 'VALID') {
            this.emailAddressIM = '';
            this.emailAddressMT = '';
            this.isValidEmailSubscription.unsubscribe();
          }
        });
      }
    }
  }

  ngOnDestroy(): void {
    if (this.isValidEmailSubscription) {
      this.isValidEmailSubscription.unsubscribe();
    }
  }
}
