import { Component, OnInit } from '@angular/core';
import { HfButtonComponent, HfCardComponent, HfModelComponent } from '@monorepo/ui';
import { Cookie, AppService, CookieService } from '@monorepo/utils';

@Component({
  selector: 'lib-app-download-popup',
  standalone: true,
  imports: [HfCardComponent, HfButtonComponent, HfModelComponent],
  templateUrl: './app-download-popup.component.html',
  styleUrl: './app-download-popup.component.scss',
})

export class HeatfleetAppDownloadPopupComponent implements OnInit {

  showModel: boolean = true;

  constructor(
    public appService: AppService,
    public cookieService: CookieService,
  ) { }

  ngOnInit() {
    this.showModel = false;
    // let popup = this.cookieService.get(Cookie.APP_POPUP);
    // if (popup == '') {
    //   this.showModel = true;
    //   this.cookieService.set(Cookie.APP_POPUP, 'true');
    // }
  }

  openAppLink(platform: 'apple' | 'android') {
    if (platform == 'apple') {
      window.open('https://apps.apple.com/us/app/heat-fleet/id6479750613', '_blank');
    } else if (platform == 'android') {
      window.open('https://play.google.com/store/apps/details?id=com.heatfleet.homeowner', '_blank');
    }
  }

  closeModel(isClosed: boolean) {
    if (isClosed) {
      this.showModel = false;
      this.cookieService.set(Cookie.APP_POPUP, 'true');
    }
  }
}
