import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})

export class HeatfleetHeaderService {

  isMenuOpen = false;
  isPageMenuOpen = false;
  isSecure: boolean = false;
  urls: any;
  clientUrl: string = '';
  isIntialized: boolean = false;
  fullLogoUrl: string = '';
  iconLogoUrl: string = '';
  nameLogoUrl: string = '';

  initHeatFleetHeader(project: 'secure' | 'seo', urls: any, client_url: string, full_logo_url: string, icon_logo_url: string, name_logo_url: string) {
    if (project === 'secure') {
      this.isSecure = true;
    } else {
      this.isSecure = false;
    }
    this.urls = urls;
    this.clientUrl = client_url;
    this.isIntialized = true;
    this.fullLogoUrl = full_logo_url;
    this.iconLogoUrl= icon_logo_url;
    this.nameLogoUrl= name_logo_url;
  }

  toggleMenu() {
    this.isMenuOpen = !this.isMenuOpen;
  }

  togglePageMenu() {
    this.isPageMenuOpen = !this.isPageMenuOpen;
  }

}
