import { CommonModule } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { RouterOutlet } from '@angular/router';
import { HfToastComponent } from '@monorepo/ui';
import { environment } from '../environments/environment';
import { AppService, AuthService, StoreService } from '@monorepo/utils';
import { HeatfleetAppDownloadPopupComponent } from '@monorepo/common-component';
import { SafeArea } from 'capacitor-plugin-safe-area';
import { App } from '@capacitor/app';
import { Meta } from '@angular/platform-browser';

@Component({
  selector: 'app-root',
  standalone: true,
  imports: [CommonModule, RouterOutlet, HfToastComponent, HeatfleetAppDownloadPopupComponent],
  templateUrl: './app.component.html',
  styleUrl: './app.component.scss'
})
export class AppComponent implements OnInit {

  constructor(
    public authService: AuthService,
    public appService: AppService,
    public storeService: StoreService,
    private meta: Meta,
  ) {
    if (this.appService.isBrowserRendering) {
      this.storeService.initStore();
    }
  }

  ngOnInit() {
    this.appService.setPlatform();
    this.authService.setAuth(environment.api_url, environment.api_version, environment.cryptojs_key, environment.cryptojs_iv);
    if (this.appService.isIosApp) {
      SafeArea.getStatusBarHeight().then(({ statusBarHeight }) => {
        document.documentElement.style.setProperty('--status-bar-height', `${statusBarHeight}px`);
      });
    }
    App.addListener('backButton', (data: any) => {
      if (data.canGoBack) {
        window.history.back();
      } else {
        App.exitApp();
      }
    });
    this.meta.updateTag({
      name: 'robots',
      content: 'noindex, nofollow',
    });
  }
}
